import * as React from 'react';
import { navigate } from 'gatsby';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import type { Property } from 'csstype';

interface IBoxContainer {
  data: any[];
}

const borderRadius: Property.BorderRadius = '5dvw';

export const BoxContainer = ({ data }: IBoxContainer) => (
  <Box
    component='section'
    sx={{
      py: 8,
      backgroundColor: 'rgb(232, 130, 122, 0.9)',
      borderRadius
    }}
  >
    <Container>
      {data.map(({ image, title, head, description, link }, i: number) => (
        <Grid container key={i} spacing={8} pb={8}>
          <Grid item sm={6}>
            <Box
              sx={{
                borderWidth: 8,
                borderColor: 'background.default',
                borderStyle: 'solid',
                borderRadius,
                overflow: 'hidden'
              }}
            >
              {image}
            </Box>
          </Grid>
          <Grid item sm={6}>
            <Box>
              <Typography
                variant='h3'
                color='background.default'
                fontWeight={700}
                sx={{
                  textDecoration: 'underline'
                }}
              >
                {title}
              </Typography>
              <Typography color='background.default'>{head}</Typography>
              <Typography color='background.default' variant='body2' mt={2}>
                {description}
              </Typography>
              {link && (
                <Box
                  mt={4}
                  display='flex'
                  justifyContent={{ md: 'flex-start', xs: 'center' }}
                >
                  <Button
                    href={link}
                    endIcon={
                      <ChevronRightIcon sx={{ width: 36, height: 36 }} />
                    }
                    variant='contained'
                    sx={{
                      backgroundColor: 'white',
                      color: 'primary.main',
                      borderRadius,
                      fontSize: 18,
                      fontWeight: 700,
                      px: 8,
                      '&:hover': {
                        backgroundColor: 'primary.main',
                        color: 'white'
                      }
                    }}
                    onClick={e => {
                      e.preventDefault();
                      if (link) {
                        navigate(link);
                      }
                    }}
                  >
                    詳しく見る
                  </Button>
                </Box>
              )}
            </Box>
          </Grid>
        </Grid>
      ))}
    </Container>
  </Box>
);
